"use client";

import Navbar from "../../components/Navbar";
import {useEffect, useState} from "react";
import {useTheme} from "../../utils/hooks/useTheme";
import Footer from "../../components/Footer";
import CoordinatorCard from "../../components/SingleCoordinatorCard";
import {
  Coordinator,
  getSingleCoordinator,
} from "../../utils/services/coordinatorService";
import {useSearchParams} from "next/navigation";

export default function ClientHome({params}: {params: {id: string}}) {
  const {theme, toggleTheme} = useTheme();
  const [coordinator, setCoordinator] = useState<Coordinator | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const searchParams = useSearchParams();

  useEffect(() => {
    const coordinatorData = searchParams.get("coordinator");

    if (coordinatorData) {
      // Fast render from passed data
      try {
        const parsed = JSON.parse(decodeURIComponent(coordinatorData));
        setCoordinator(parsed);
        setLoading(false);
      } catch (err) {
        console.error("Parsing failed, falling back to server fetch:", err);
        fetchFromServer();
      }
    } else {
      // Full page reload or direct URL visit
      fetchFromServer();
    }
  }, [params.id]);

  const fetchFromServer = async () => {
    setLoading(true);
    try {
      const data = await getSingleCoordinator(params.id);
      setCoordinator(data);
    } catch (err) {
      console.error("Server fetch failed:", err);
      setError("Failed to load coordinator details.");
    } finally {
      setLoading(false);
    }
  };

  const [showBookingForm, setShowBookingForm] = useState<boolean>(false);


  if (loading) return <p className='text-center'>Loading...</p>;
  if (error) return <p className='text-center text-red-500'>{error}</p>;
  if (!coordinator)
    return <p className='text-center'>Coordinator not found.</p>;

  return (
    <div className='min-h-screen bg-white dark:bg-gray-800 antialiased transition-colors duration-300'>
      <Navbar theme={theme} toggleTheme={toggleTheme} />
      {/* <section className='py-22 px-6 max-w-5xl mx-auto'>
        <h2 className='text-3xl font-bold mb-10 text-center'>
          Coordinator Details
        </h2>
        <CoordinatorCard coordinator={coordinator} />
      </section> */}
      <section className='py-22 px-6 max-w-7xl mx-auto'>
        <h2 className='text-3xl font-bold mb-10 text-center'>
          Coordinator Details
        </h2>

        <div
          className={`grid grid-cols-1 md:grid-cols-2 gap-6 transition-all duration-300`}
        >
          {/* Left: Coordinator Card */}
          {!showBookingForm || window.innerWidth >= 768 ? (
            <CoordinatorCard
              coordinator={coordinator}
              onBookNow={() => setShowBookingForm(true)}
            />
          ) : null}

          {/* Right: Booking Form */}
          {showBookingForm && (
            <div className='bg-white dark:bg-gray-900 rounded-lg shadow-md p-6 flex flex-col justify-between'>
              <h3 className='text-2xl font-semibold mb-4'>
                Book This Coordinator
              </h3>
              <form className='space-y-4'>
                <input
                  type='text'
                  placeholder='Your Name'
                  className='w-full p-3 rounded-md border border-gray-300'
                />
                <input
                  type='email'
                  placeholder='Your Email'
                  className='w-full p-3 rounded-md border border-gray-300'
                />
                <input
                  type='date'
                  placeholder='Wedding Date'
                  className='w-full p-3 rounded-md border border-gray-300'
                />
                <input
                  type='number'
                  placeholder='Guest Number'
                  className='w-full p-3 rounded-md border border-gray-300'
                />
                <button
                  type='submit'
                  className='w-full py-3 rounded-md bg-purple-600 text-white hover:bg-purple-700 transition'
                >
                  Confirm Booking
                </button>
              </form>
            </div>
          )}
        </div>
      </section>

      <Footer />
    </div>
  );
}
